.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#nav{
  display: block;
  background: #21262d;
  padding: 10px;
  border: none;
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  align-items: center;
  color:#971ad0;
  font-weight: bold;

}
#addNewMatrix{
  border: navajowhite;
  font-family: monospace;
  background: #9014d3;
  color: white;
  border-radius: 4px;
  padding: 7px;
  right: 20px;
  cursor: pointer;
}